* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.container h1 {
    text-align: center;
    margin-bottom: 20px;
}

.joke-box {
    padding: 20px 20px 20px 50px;
    width: 660px;
    min-height: 200px;
    border-radius: 10px;
    margin-bottom: 25px;
    font-size: 1.35rem;
    color: white
}

.joke-box .joke-setup {
    margin-bottom: 17px;
    position: relative;
}

.joke-box .joke-setup::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
    left: -25px;
    top: 50%;
    transform: translateY(-50%)
}

.joke-box .joke-delivery {
    position: relative;
}

.joke-box .joke-delivery::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 15px;
    background-color: red;
    border-radius: 50%;
    left: -25px;
    top: 50%;
    transform: translateY(-50%)
}

.categories-btns {
    width: 720px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    row-gap: 20px;
}

.categories-btns div {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.categories-btns div button {
    padding: 5px 10px;
    border: none;
    outline: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 1.1rem;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.5);
    transition: 1s;
}

.categories-btns div button:hover {
    box-shadow: none;
}

.targetCategory {
    background-color: #191D88;
    color: #fff;
}

.jokes-language {
    margin-top: 30px;
    width: 600px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.jokes-language div {
    font-size: 1.3rem;
}

.jokes-language div input {
    width: 20px;
    height: 20px;
    margin: 10px;
    cursor: pointer;
}